.factoryForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.factoryInput__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    margin-bottom: 20px;
    width: 100%;
}

.factoryInput__input {
    flex-grow: 1;
    height: 40px;
    padding: 0px 20px;
    color: white;
    border: 1px solid #a29bfe;
    border-radius: 20px;
    font-weight: 500;
    font-size: 12px;
}

.factoryInput__arrow {
    position: absolute;
    right: 0;
    background-color: #a29bfe;
    height: 40px;
    width: 40px;
    padding: 10px 0px;
    text-align: center;
    border-radius: 20px;
    color: white;
    box-sizing: border-box;
    cursor: pointer;
}

.factoryInput__label {
    color: #a29bfe;
    cursor: pointer;
}

.factoryInput__label span {
    margin-right: 10px;
    font-size: 12px;
}

.factoryForm__attachment {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.factoryForm__attachment img {
    height: 120px;
    width: auto;
    border-radius: 10px;
}

.factoryForm__clear {
    background-color: black;
    border: none;
    color: #ff7675;
    cursor: pointer;
    text-align: center;
}

.factoryForm__clear span {
    margin-right: 10px;
    font-size: 12px;
}

.factoryInput__input__image {
    opacity: 0;
}

.factoryInput__input::placeholder {
    color: white;
    opacity: 0.9;
}
