@import "https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css";

input {
    all: unset;
    appearance: none;
    box-sizing: border-box;
}

* {
    box-sizing: border-box;
}

body {
    background-color: black;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    font-size: 14px;
    color: white;
}

button {
    background-color: white;
    color: black;
}

a {
    text-decoration: none;
    color: inherit;
}

form {
    width: 100%;
}

.container {
    width: 100%;
    max-width: 320px;
    display: flex;
    flex-direction: column;
}

.formInput {
    width: 100%;
    padding: 10px 20px;
    border-radius: 20px;
    border: 1px solid black;
    text-align: center;
    background-color: white;
    color: black;
}

.formBtn {
    cursor: pointer;
    width: 100%;
    padding: 7px 20px;
    text-align: center;
    color: white;
    border-radius: 20px;
    background-color: #04aaff;
    cursor: pointer;
}

.cancelBtn {
    cursor: pointer;
    background-color: tomato;
}

footer {
    position: absolute;
    bottom: 50px;
    right: 20px;
    text-align: right;
    margin: 30px 0px;
}

::placeholder {
    color: #bdc3c7;
}
