.authInput {
    max-width: 300px;
    width: 100%;
    padding: 10px;
    border-radius: 30px;
    background-color: rgba(255, 255, 255, 1);
    margin-bottom: 10px;
    font-size: 12px;
    color: black;
    box-sizing: content-box;
}

.authSubmit {
    text-align: center;
    background: #a29bfe;
    color: white;
    margin-top: 10px;
    box-sizing: content-box;
    cursor: pointer;
}

.authError {
    color: tomato;
    text-align: center;
    font-weight: 500;
    font-size: 12px;
}

.authSwitchContainer {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 300px;
}

.authSwitch {
    color: #04aaff;
    cursor: pointer;
    margin-top: 10px;
    margin-bottom: 50px;
    display: block;
    font-size: 12px;
    text-decoration: underline;
}
