    cursor: pointer;
}

.pangEdit .formBtn {
    cursor: pointer;
    margin-top: 15px;
    margin-bottom: 5px;
}

.pang {
    margin-bottom: 40px;
    background-color: white;
    width: 100%;
    max-width: 320px;
    padding: 20px;
    border-radius: 20px;
    position: relative;
    display: flex;
    flex-direction: column;
    color: rgba(0, 0, 0, 0.8);
    box-shadow: 1px 1px 3px rgba(255, 255, 255, 0.8),
        1px 1px 2px rgba(255, 255, 255, 0.7);
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
    -webkit-animation: fadeInOut ease 9s forwards;
            animation: fadeInOut ease 9s forwards;
}

@-webkit-keyframes fadeInOut {
    0% {
        opacity: 0;
        top: -50px;
    }
    10%,
    80% {
        top: 0px;
        opacity: 1;
        transform: scale(1);
    }
    100% {
        opacity: 0;
        transform: scale(0.5);
    }
}

@keyframes fadeInOut {
    0% {
        opacity: 0;
        top: -50px;
    }
    10%,
    80% {
        top: 0px;
        opacity: 1;
        transform: scale(1);
    }
    100% {
        opacity: 0;
        transform: scale(0.5);
    }
}

.pang:hover {
    transform: scale(1.01);
    box-shadow: 5px 8px 16px rgba(255, 255, 255, 0.8),
        5px 5px 5px rgba(255, 255, 255, 1);
}

.pang h4 {
    font-size: 14px;
}

.pang img {
    right: -10px;
    top: 15px;
    position: absolute;
    border-radius: 10px;
    width: auto;
    height: 60px;
    margin-top: 10px;
}

.pang__actions {
    position: absolute;
    right: 20px;
    top: 8px;
}

.pang__actions span {
    cursor: pointer;
}

.pang__actions span:first-child {
    margin-right: 10px;
}

.factoryForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.factoryInput__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    margin-bottom: 20px;
    width: 100%;
}

.factoryInput__input {
    flex-grow: 1;
    height: 40px;
    padding: 0px 20px;
    color: white;
    border: 1px solid #a29bfe;
    border-radius: 20px;
    font-weight: 500;
    font-size: 12px;
}

.factoryInput__arrow {
    position: absolute;
    right: 0;
    background-color: #a29bfe;
    height: 40px;
    width: 40px;
    padding: 10px 0px;
    text-align: center;
    border-radius: 20px;
    color: white;
    box-sizing: border-box;
    cursor: pointer;
}

.factoryInput__label {
    color: #a29bfe;
    cursor: pointer;
}

.factoryInput__label span {
    margin-right: 10px;
    font-size: 12px;
}

.factoryForm__attachment {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.factoryForm__attachment img {
    height: 120px;
    width: auto;
    border-radius: 10px;
}

.factoryForm__clear {
    background-color: black;
    border: none;
    color: #ff7675;
    cursor: pointer;
    text-align: center;
}

.factoryForm__clear span {
    margin-right: 10px;
    font-size: 12px;
}

.factoryInput__input__image {
    opacity: 0;
}

.factoryInput__input::-webkit-input-placeholder {
    color: white;
    opacity: 0.9;
}

.factoryInput__input:-ms-input-placeholder {
    color: white;
    opacity: 0.9;
}

.factoryInput__input::-ms-input-placeholder {
    color: white;
    opacity: 0.9;
}

.factoryInput__input::placeholder {
    color: white;
    opacity: 0.9;
}

